.footer {
  width: 100%;
  background-color: #f2f2f2;
  border-bottom: 1px solid @border-color;

  @media @tablet-width {
    min-height: 150px;
  }
}

.footer__container {
  padding-top: 37px;

  @media @tablet-width {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 29px;
  }

  @media @desktop-width {
    // width: 100%;
    min-height: 150px;
    align-items: center;
    padding: 0 39px;
  }
}

