.footer__logo-link {
  text-align: center;
  // position: relative;
  // left: 27%;
  // margin: 0 auto;
  // padding-top: 29px;
  box-sizing: border-box;
  height: auto;
  display: block;
  // border-bottom: 1px solid #d9d9d9;

  @media @mobile-width {
    margin: 0 auto;
    height: 56px;
  }

  @media @tablet-width {
    // height: 25px;
    border: none;
    left: 0;
  }

  &:hover,
  &:focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.footer__social {
  border-top: 1px solid @border-color;
  // width: 96%;
}

.footer__html-academy {
  border-top: 1px solid @border-color;
  width: 100%;

  @media @tablet-width {
    border: none;
  }
}
